import { Injectable } from '@angular/core';
import { Usuario } from '../../models/usuario.model';
import { HttpClient } from '@angular/common/http'
import { URL_SERVICES } from '../../config/config';
import  { catchError, map } from 'rxjs/operators';

import { throwError } from 'rxjs';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';



@Injectable({
  providedIn: 'root'
})
export class LoginService {

  // Definimos una variable global "token" para despus asignarle un valor
  token: string;

  constructor( public router: Router, public http: HttpClient ) {
   }

  // Esta funcin nos va a servir para cuando el estudiante est en la pgina de requisitos
  // y si intenta entrar al panel principal, no lo deje. Ya que la ruta va a estar protegida
  // por el loginGuard que confirma si est el token en el local storage --> Al hacer click
  // En el botn continuar, se llama esta fincin y se guarda el token en el local storage
  dejaPasarModalidades(){
    localStorage.setItem('token', this.token);
  }


  // Devuelve una bandera (true/false) confirmando si el usuario est logueado (tiene el token
  // en el local storage)
  logueado(){
    this.token = localStorage.getItem("token");
    return ( this.token )? true: false;
  }

  // Redirije a la pagina del login
  logOut(){
    this.router.navigate(['/login']);
  }

  // En esta funcin se llama al servicio del login del backend
  // Recibe un usuario de tipo Usuario (Modelo), y una bandera que nos indica que el check de
  // recordarme est activado

  login( usuario: Usuario, recordar: boolean = false){

    // Definimos la url del servicio que queremos llamar
    let url = URL_SERVICES + '/login';

    // Enviamos los datos. Es un observable, entonces tendr una respuesta
    return this.http.post(url, usuario)
    .pipe(map( (resp:any) => {

      if (resp['estudiante']){

        // Si el check est activado, se guarda el usuario en el localStorage
        if (recordar === true) {
          localStorage.setItem('usuario', resp.estudiante.usuario);
        } else {
          localStorage.removeItem('usuario')
        }

        // Guardamos toda la info del estudiante y el id
        localStorage.setItem('estudiante', JSON.stringify(resp.estudiante));
        localStorage.setItem('id', resp.estudiante._id);

        // Se le asigna el token de la respuesta a la variable token
        this.token = resp.token;



        // Es el mismo procedimiento anterior(Administrativo)
      } else {

        // Si el check est activado, se guarda el usuario en el localStorage
        if (recordar === true) {
          localStorage.setItem('usuario', resp.administrativo.usuario);
        } else {
          localStorage.removeItem('usuario');
        }

        // Se guarde la info del administrativo, el id y el token al local sotrage
        // NOTA: Como nos podemos dar cuenta, aqu no se asigna el token del admin a la variable
        // token, esto debido a que el administrativo no va a pasar por la pgina de requisitos
        // y necesita el token a penas se loguee en el local storae
        localStorage.setItem('administrativo', JSON.stringify(resp.administrativo));
        localStorage.setItem('id', resp.administrativo._id);
        localStorage.setItem('token', resp.token);

      }

      return true;
  }),
      // Si existen errores (status code de la peticin), los capturamos y los ponemos en un
      // sweetalert
      catchError((err) => {

      Swal.fire({
        title:'Error!',
        text: err.error.mensaje,
        icon:'error',
        confirmButtonColor: '#8F141B'
      });

      return throwError(err);

  }));
  }

}
